@import url("https://fonts.googleapis.com/css?family=Black+Ops+One&display=swap");

body {
    margin: 0;
    padding: 0;
    font-family: "Courier New", Courier, monospace;
    background: rgb(57, 55, 98);
    background: linear-gradient(
        90deg,
        rgba(57, 55, 98, 1) 0%,
        rgba(101, 162, 173, 1) 100%
    );
}

h1{
    font-family: "Black Ops One", cursive;
    color:white;
    text-transform: uppercase;
}

.header {
    font-size: 5rem;
    font-weight: 300;
}

.scroll{
    overflow-y: scroll;
    border: 4px solid rgb(57, 55, 98);
    height: 800px;
}

